import * as THREE from "three";

export function initCamera() {
    let camera = new THREE.PerspectiveCamera();
    camera.name = "CAMERA";
    camera.fov = 45; //fov0180504545
    // camera.aspect = width / height; //aspect1
    camera.near = 0.1; //near0.11
    camera.far = 1000; //far2000
    camera.position.x = 0;
    camera.position.y = 100;
    camera.position.z = 0;
    return camera;
}